import React from 'react';
import { Link } from 'react-router-dom';
import { lang } from '../../../lang/lang';

const Archivo = (param) => {
    const {key, path, de, co, fecha, tipo, obs, idx} = param

    let imgAux = require('../../../assets/images/no_image.png').default
    if( false && tipo == 'img' && (de !== null || de !== '' ))
        imgAux = require('../../../assets/' +path +'/' +idx +'/' +de).default
// ../../../assets/images/activo/1/vehiculo-01.jpg
imgAux = require('../../../assets/images/activo/1/vehiculo-01.jpg').default

    return (
        <React.Fragment>
            <tr className="unread">
                <td style={{width: '2.0em'}}>
                    <Link to="#" className="" style={{padding: '0.5em'}} title={lang.editar}>
                        <i className='feather icon-edit mx-1' style={{fontSize: '1.5em'}}/>
                    </Link>
                    <Link to="#" className="" style={{padding: '0.5em'}} title="Acceder">
                        <i className='feather icon-eye mx-1' style={{fontSize: '1.5em'}}/>
                    </Link>
                    <Link to="#" className="" style={{padding: '0.5em'}} title="Eliminar">
                        <i className='feather icon-x mx-1' style={{fontSize: '1.5em'}}/>
                    </Link>
                </td>
                <td style={{width: '6.0em'}}>
                    <h6 className="mb-1">{obs}</h6>
                    <h6 className="text-muted">{fecha}</h6>
                </td>
                <td><img style={{ width: '400px' }} src={imgAux} alt="" /></td>
                <td>&nbsp;</td>
            </tr>
        </React.Fragment>
    );
};
export default Archivo;
