import React from 'react';
import { Link } from 'react-router-dom';
// import { Card, Table } from 'react-bootstrap';
import { Table } from 'react-bootstrap';
import { lang } from '../../../lang/lang';
import Card from '../../../components/Card/Card1';
import {activos} from './x';
import AlertaDetalle from './AlertaDetalle';

import { ListGroup, Dropdown, Media, Button } from 'react-bootstrap';

const liAle = {
    display:'inline-block', 
    marginLeft: '0.5em', 
    marginRight: '0.5em'
}

const Eventos = (promp) => {
    const {tp, id} = promp
    let ale = []

    for(let i=0; i < activos[id].alertas.length ;i++){
        if( parseInt(tp) > 0 ){
            if( parseInt(activos[id].alertas[i].tp) == parseInt(tp) -1 )
            ale.push(<AlertaDetalle 
                key={i} 
                img={activos[id].alertas[i].img} 
                bg={activos[id].alertas[i].bg} 
                tp={activos[id].alertas[i].tpde}
                origen={activos[id].alertas[i].origen} 
                de={activos[id].alertas[i].de} 
                fecha={activos[id].alertas[i].fecha} 
                btn1={activos[id].alertas[i].btn1} 
                btn2={activos[id].alertas[i].btn2} 
            />)
        }else{
            ale.push(<AlertaDetalle 
                key={i} 
                img={activos[id].alertas[i].img} 
                bg={activos[id].alertas[i].bg} 
                tp={activos[id].alertas[i].tpde}
                origen={activos[id].alertas[i].origen} 
                de={activos[id].alertas[i].de} 
                fecha={activos[id].alertas[i].fecha} 
                btn1={activos[id].alertas[i].btn1} 
                btn2={activos[id].alertas[i].btn2} 
            />)   
        }
    }
    // tipo d alerta
    let idx = 7
    return (
        <React.Fragment>
            <Card title={lang.eventos} isOption isPlus isDown isLoad>
                <Table responsive hover>
                    <tbody>
                        {ale}
                    </tbody>
                </Table>
            </Card>
        </React.Fragment>
    );
};
export default Eventos;