const Diagnosticos = (promp) => {
    const {tp, id} = promp
    let ale = []

    let subseccion_act = null
    let subseccion_ant = null
    let subseccion_pub = null
    let seccion_act = null 
    let seccion_ant = null
    let seccion_pub = null
    let obs_act = null 
    let obs_ant = null
    let obs_pub = null
    let de_act = null
    let de_ant = null
    let de_pub = null
    let valor_act = null
    let valor_ant = null
    let valor_pub = null
    let icon_act = null
    let icon_ant = null
    let icon_pub = null


    ale.push(<Diagnostico1 
        key={0} 
        seccion={'Carga de Información'} 
        subseccion={''} 
        obs={'Parameros obligatorios'} 
        de={''} 
        valor={''} 
        icon={'fa-truck'}
    />);
    let u = 0;
    for(let i=0; i < activos[id].parametros.length ;i++){

        if( activos[id].parametros[i].valor !== null && activos[id].parametros[i].valor.length > 0 ){
            const last = activos[id].parametros[i].valor.length -1;
            // if( (activos[id].parametros[i].valor[last].de === null || activos[id].parametros[i].valor[last].de == '') && activos[id].parametros[i].tp == '1' ){
                if( activos[id].parametros[i].tp == '1' ){
           
                // tp={activos[id].tp}
                // obs={activos[id].parametros[i].txt}
                // tipo={activos[id].parametros[i].valor[last].tp}
            
            u = i +1
            ale.push(<Diagnostico1 
                key={u} 
                seccion={''} 
                subseccion={''} 
                obs={''} 
                de={activos[id].parametros[i].de} 
                valor={activos[id].parametros[i].valor[last].de} 
                icon={activos[id].parametros[i].icon}
            />);
            }
        }
    }




    for(let i=0; i < activos[id].diagnostico.length ;i++){
        seccion_act = activos[id].diagnostico[i].de
        obs_act = activos[id].diagnostico[i].txt
        icon_act = activos[id].diagnostico[i].icon
        for(let j=0; j < activos[id].diagnostico[i].bag.length ;j++){
            if( activos[id].diagnostico[i].bag.length > 0 && activos[id].diagnostico[i].bag[j].item !== null )
                for(let k=0; k < activos[id].diagnostico[i].bag[j].item.length ;k++){
                    if( seccion_act !== seccion_ant ){
                        seccion_ant = seccion_act
                        seccion_pub = seccion_act
                        obs_pub = obs_act
                        icon_pub = icon_act
                        de_pub = activos[id].diagnostico[i].bag[j].item[k].de
                        valor_pub = activos[id].diagnostico[i].bag[j].item[k].valor
                    }else{
                        seccion_pub = ''
                        obs_pub = ''
                        icon_pub = icon_act
                        de_pub = activos[id].diagnostico[i].bag[j].item[k].de
                        valor_pub = activos[id].diagnostico[i].bag[j].item[k].valor
                    }
                    if( activos[id].diagnostico[i].bag[j].item[k].de == 'tipo' && subseccion_act != activos[id].diagnostico[i].bag[j].item[k].valor ){
                        subseccion_act = valor_pub = activos[id].diagnostico[i].bag[j].item[k].valor
                        subseccion_pub = subseccion_act
                        de_pub = ''
                        valor_pub = ''
                    }else{
                        subseccion_pub = ''
                    }
                    if( activos[id].diagnostico[i].bag[j].item[k].de != 'st' ){
                        u += k +1
                        if( seccion_pub != '' && subseccion_pub != '' ){
                            ale.push(<Diagnostico1 
                                key={u} 
                                seccion={seccion_pub} 
                                subseccion={''} 
                                obs={obs_pub} 
                                de={de_pub} 
                                valor={valor_pub} 
                                icon={activos[id].diagnostico[i].icon}
                            />); 
                            ale.push(<Diagnostico1 
                                key={u} 
                                seccion={''} 
                                subseccion={subseccion_pub} 
                                obs={''} 
                                de={de_pub} 
                                valor={valor_pub}
                                icon={activos[id].diagnostico[i].bag[j].item[k].icon}
                                tp={activos[id].diagnostico[i].bag[j].item[k].tp}
                            />); 
                        }else{
                            ale.push(<Diagnostico1 
                                key={u} 
                                seccion={seccion_pub} 
                                subseccion={subseccion_pub} 
                                obs={obs_pub} 
                                de={de_pub} 
                                valor={valor_pub}
                                icon={activos[id].diagnostico[i].bag[j].item[k].icon}
                                tp={activos[id].diagnostico[i].bag[j].item[k].tp}
                            />);  
                        } 
                    }

                }
        }

    }

    /*
    for(let i=0; i < activos[0].diagnostico.length ;i++){
        for(let j=0; j < activos[0].diagnostico[i].bag.length ;j++){
            if( activos[0].diagnostico[i].bag.length > 0 && activos[0].diagnostico[i].bag[j].item !== null )
                for(let k=0; k < activos[0].diagnostico[i].bag[j].item.length ;k++){
                    ale.push(<Diagnostico 
                        key={k} 
                        seccion={activos[0].diagnostico[i].de} 
                        obs={activos[0].diagnostico[i].txt} 
                        de={activos[0].diagnostico[i].bag[j].item[k].de} 
                        valor={activos[0].diagnostico[i].bag[j].item[k].valor} 
                    />);
                }
        }
    }
*/


    return (
        <React.Fragment>
            <Card1 title={lang.diagnostico} isDown>
                <Table responsive hover>
                    <tbody>
                        {ale}
                    </tbody>
                </Table>
            </Card1>
        </React.Fragment>
    );
};
export default Diagnosticos;