import React from 'react';
import { lang } from '../../../lang/lang';

const Diagnostico1 = (param) => {
    const {key, seccion, subseccion, obs, de, valor, icon, tp} = param
    let imgAux = require('../../../assets/images/no_image.png').default
    const c1 = '#ff0000';
    let icon0 = 'fa fa-angle-down f-15 m-r-15'
    
    const formatter = new Intl.NumberFormat('es-CO', {
        style: 'currency',
        currency: 'COP',
        // These options are needed to round to whole numbers if that's what you want.
        //minimumFractionDigits: 0, // (this suffices for whole numbers, but will print 2500.10 as $2,500.1)
        //maximumFractionDigits: 0, // (causes 2500.99 to be printed as $2,501)
      });
      
    const fecha = (p) => {
        return new Date(p).toLocaleDateString('es-co', { year:"numeric", month:"short", day:"numeric"}) 
    }

 
   

    if( icon !== null && icon != '' ) icon0 = 'fa ' +icon +' f-15 m-r-15'
    return (
        <React.Fragment>
            <tr className="unread">
                <td>
                {(seccion != '')?
                    <React.Fragment>
                        <h6 className="mb-1">
                            <i className={icon0} style={{fontFamily: 'fontAwesome', color: c1}} title={''} /> 
                            {seccion}</h6>
                        <p className="m-0"><i>{obs}</i></p>
                    </React.Fragment>:
                    (subseccion != '')?
                    <React.Fragment>
                        <h6 className="mb-1" style={{textIndent: '3.0em'}}>
                            <i className={icon0} style={{fontFamily: 'fontAwesome', color: c1}} title={''} /> 
                            {subseccion}
                            </h6>
                    </React.Fragment>:
                    <React.Fragment>
                        <h5 style={{textIndent: '6.0em'}}>
                            <p className="m-0"><i className={icon0} style={{color: c1}} title={''} /> 
                                <span className="m-0" style={{color: '#888888'}}>{de}</span> &nbsp;
                                <b>
                                {(tp == 'monto')?formatter.format(valor):(tp == 'fecha')?fecha(valor):valor}
                                </b>
                            </p>
                        </h5>
                    </React.Fragment>}
                </td>
            </tr>  
        </React.Fragment>
    );
};
export default Diagnostico1;

