import React from 'react';
import { Link } from 'react-router-dom';
import { lang } from '../../../lang/lang';

const Parametro = (param) => {
    const {key, idx, tp, de, valor, fecha, co, obs, tipo} = param
    let valor2 = valor
    if( tipo == 'imagen' && valor !== null ){
        const img1 = require('../../../assets/images/activo/' +idx +'/' +valor).default
        valor2 = <img classN88ame="rounded-circle" style={{ maxHeight: '10em', maxWidth: '10em' }} src={img1} alt="" />
    }
    return (
        <React.Fragment>
            <tr className="unread">
                <td style={{width: '2.0em'}}>
                    <Link to="#" className="" style={{padding: '0.5em'}} title={lang.editar}>
                        <i className='feather icon-edit mx-1' style={{fontSize: '1.5em'}}/>
                    </Link>
                </td>
                <td style={{width: '2.5em'}}>
                    <h6 className="text-muted">
                        <i className="fa fa-circle f-10 m-r-15" style={{color: co}} title={tp} />
                        {fecha}
                    </h6>
                </td>
                <td>
                    <p className="mb-1">{de}</p>
                    <h6 className="mb-1">{valor2}</h6>
                </td>
                <td>
                    <p className="m-0">{obs}</p>
                </td>
            </tr>
        </React.Fragment>
    );
};
export default Parametro;
