import React from 'react';
import { Table } from 'react-bootstrap';
import { lang } from '../../../lang/lang';
import Card1 from '../../../components/Card/Card1';
import {activos} from './x';
import Archivo from './Archivo';

const Archivos = (promp) => {
    const {tp, id} = promp
    let ale = []

    for(let i=0; i < activos[id].archivos.length ;i++){
            ale.push(<Archivo 
                key={activos[id].archivos[i].id} 
                path={activos[id].archivos[i].path}
                de={activos[id].archivos[i].src} 
                obs={activos[id].archivos[i].txt} 
                tipo={activos[id].archivos[i].tipo} 
                fecha={activos[id].archivos[i].fecha} 
                idx={activos[id].id}  
            />)
    }

    return (
        <React.Fragment>
            <Card1 title={lang.parametros} isOption isPlus isDown isLoad>
                <Table responsive hover>
                    <tbody>
                        {ale}
                    </tbody>
                </Table>
            </Card1>
        </React.Fragment>
    );
};
export default Archivos;