import React, { useState } from 'react';
import { Dropdown, Card, Collapse } from 'react-bootstrap';
import { Link } from 'react-router-dom';

import useWindowSize from '../../hooks/useWindowSize';

const Card2 = (props) => {
    const { isOption, isPlus, isDown, isLoad, title, children, cardClass, optionClass } = props;

    const [fullCard, setFullCard] = useState(false);
    const [collapseCard, setCollapseCard] = useState(false);
    const [loadCard, setloadCard] = useState(false);
    const [cardRemove, setCardRemove] = useState(false);

    const windowSize = useWindowSize();

    const cardReloadHandler = () => {
        setloadCard(true);
        setInterval(() => {
            setloadCard(false);
        }, 3000);
    };

    const cardRemoveHandler = () => {
        setCardRemove(true);
    };

    let fullScreenStyle = { padding: '0px', margin: '0px', top: -28, left: -28, right: 0 };
    let loader, cardHeaderRight, cardHeader;
    let card = '';
    let mainCardClass = [];

    if (isOption) {
        cardHeaderRight = (
            <div className={'card-header-right ' + optionClass}>
                <Dropdown alignRight={true} className="btn-group card-option">
                    <Dropdown.Toggle id="plus" className="btn-icon" onClick={() => setFullCard(!fullCard)}>
                        <i className={isPlus ? 'feather icon-plus' : ''} style={{fontSize: '1.5em'}} />
                    </Dropdown.Toggle>
                    <Dropdown.Toggle id="plus" className="btn-icon" onClick={() => setFullCard(!fullCard)}>
                        <i className={isDown ? 'feather icon-download' : ''} style={{fontSize: '1.5em'}} />
                    </Dropdown.Toggle>
                    <Dropdown.Toggle id="plus" className="btn-icon" onClick={() => setFullCard(!fullCard)}>
                        <i className={isLoad ? 'feather icon-external-link' : ''} style={{fontSize: '1.5em'}} />
                    </Dropdown.Toggle>
                    <Dropdown.Toggle id="max" className="btn-icon" onClick={() => setFullCard(!fullCard)}>
                        <i className={fullCard ? 'feather icon-minimize' : 'feather icon-maximize'} style={{fontSize: '1.5em'}} />
                    </Dropdown.Toggle>
                </Dropdown>
            </div>
        );
    }

    cardHeader = (
        <Card.Header>
            <Card.Title as="h5">{title}</Card.Title>
            {cardHeaderRight}
        </Card.Header>
    );

    if (fullCard) {
        mainCardClass = [...mainCardClass, 'full-card'];
        fullScreenStyle = { position: 'fixed', top: 0, left: 0, right: 0, width: windowSize.width, height: windowSize.height };
    }

    if (loadCard) {
        mainCardClass = [...mainCardClass, 'card-load'];
        loader = (
            <div className="card-loader">
                <i className="pct-loader1 anim-rotate" />
            </div>
        );
    }

    if (cardRemove) {
        mainCardClass = [...mainCardClass, 'd-none'];
    }

    if (cardClass) {
        mainCardClass = [...mainCardClass, cardClass];
    }

    card = (
        <Card className={mainCardClass.join(' ')} style={fullScreenStyle} onClick={() => setFullCard(!fullCard)}>
            <Collapse in={!collapseCard}>
                <div>
                    <Card.Body>{children}</Card.Body>
                </div>
            </Collapse>
            {loader}
        </Card>
    );

    return <React.Fragment>{card}</React.Fragment>;
};

export default Card2;