import React from 'react';
import { Table } from 'react-bootstrap';
import { lang } from '../../../lang/lang';
import Card1 from '../../../components/Card/Card1';
import {activos} from './x';
import Parametro from './Parametro';

const Parametros = (promp) => {
    const {tp, id} = promp
    let ale = []

    for(let i=0; i < activos[id].parametros.length ;i++){
        let color = '#ffffff' 
        
        // const last = activos[id].parametros[i].data.length -1;
        if( activos[id].parametros[i].valor !== null && activos[id].parametros[i].valor.length > 0 ){
            const last = activos[id].parametros[i].valor.length -1;
            if( (activos[id].parametros[i].valor[last].de === null || activos[id].parametros[i].valor[last].de == '') && activos[id].parametros[i].tp == '1' )
                color = '#ff0000'
            ale.push(<Parametro 
                key={activos[id].parametros[i].id}
                idx={activos[id].id}
                tp={activos[id].tp}
                de={activos[id].parametros[i].de} 
                obs={activos[id].parametros[i].txt} 
                valor={activos[id].parametros[i].valor[last].de} 
                fecha={activos[id].parametros[i].valor[last].fecha}
                co={color} 
                tipo={activos[id].parametros[i].valor[last].tp}
            />)
        }
    }
    
    return (
        <React.Fragment>
            <Card1 title={lang.parametros} isOption isPlus isDown isLoad>
                <Table responsive hover>
                    <tbody>
                        {ale}
                    </tbody>
                </Table>
           </Card1>
        </React.Fragment>
    );
};
export default Parametros;